body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
/*     font-family: 'Asap', sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1, h2, h3 {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}
.App {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
}

.fixedOverlay {
    position: fixed;
    background-color: rgba(180, 180, 180, 0.8);
    height: 100vh;
    width: 100vw;
    padding-top: 70px;
    text-align: end;
}

.fixedOverlay i {
    margin: 20px 50px 20px 0;
    color: white;
    opacity: 0.8;
    cursor: pointer;
}

.foHidden {
    display: none;
}

.foVisible {
    display: block;
}

.foContent {
    max-width: 90%;
    max-height: 90vh;
    margin: 0 auto;
}

.foContent img {
    display: block;
    max-width: 90%;
    margin: 0 auto;
}
.sidebarTopBarWrapper {
    position: fixed;
    background-color: white;
    width: 100%;
    height: 60px;
    z-index: 98;
}

.sidebarTopBar {
    width: calc(100% - 40px);
    height: 60px;
    margin: 0 auto;
    background-color: white;
    border-bottom: 1px solid lightgray;
}

.sidebarOpener {
    display: inline-block;
    margin-left: 5px;
    padding: 15px;
    cursor: pointer;
    color: #666;
}

.sidebarTopBarLogo {
    display: inline-block;
    height: 55px;
    position: absolute;
    top: 2px;
    right: 30px;
}

.sidebarBackground {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(80, 80, 80, 0.7);
    display: none;
    z-index: 99;
}

.sidebarBgVisible {
    display: block;
}

.sidebar {
    position: fixed;
    background-color: white;
    width: 250px;
    height: 100%;
    transition: left 0.3s ease;
    z-index: 100;
}

.sidebarOpen {
    left: 0px;
}

.sidebarClosed {
    left: -250px;
}

.sidebarHeader {
    display: flex;
    height: 60px;
}

.sidebarLangSwitch {
    font-size: 16px;
    font-weight: 600;
    margin: 10px 0 10px 20px;
    padding: 10px;
    cursor: pointer;
    background-color: #08415c;
    color: white;
    border-radius: 20px;
}

.sidebarLangSwitch h4 {
    margin: 0;
    padding: 0;
}

.sidebarCloser {
    position: absolute;
    padding: 15px;
    margin-left: 180px;
    cursor: pointer;
}

.sidebarContentWrapper {
    overflow-y: auto;
    height: 100%;
}

.sidebarContent {
    margin: 50px 0 100px 20px;
}

.sidebarLink {
    text-decoration: none;
}

.sidebarMainLink {
    color: #333;
}

.sidebarSubLinkContainer {
    margin-left: 20px;
}

.sidebarSubLink {
    color: #666;
    margin: 10px 15px 0 0;
    padding: 10px;
    border-left: 10px solid #efc51f;
    border-bottom: 1px solid #efc51f;
}
.topNavBar {
    background-color: white;
    position: fixed;
    height: 60px;
    width: 100%;
    z-index: 100;
}

.navElementWrapper {
    display: flex;
    align-items: center;
    height: 60px;
    width: 95%;
    margin: 0 auto;
    border-bottom: 1px solid lightgray;
}

.navLogo {
    height: 55px;
}

.navElementContainer {
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
    padding: 0 120px 0 20px;
    display: grid;
    grid-auto-flow: column;
    align-content: center;
    justify-items: center;
}

.langSelector {
    min-width: 80px;
    cursor: pointer;
    color: rgb(131, 131, 131);
    font-size: 0.9rem;
}

.langSelector:hover {
    text-decoration: underline;
}

@media only screen and (max-width: 900px){
    .navElementContainer {
        padding: 0 20px;
    }
}
.tnbElemLink {
    text-decoration: none;
}

.tnbElem {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 140px;
    height: 40px;
    cursor: pointer;
    color: black;
    text-decoration: none;
}

.tnbElem span {
    display: block;
    font-size: 20px;
    font-weight: 500;
    text-align: center;
}


.tnbElemOpen {
    background-color: #08415c;
    color: white;
    border-radius: 30px;
}
.subNavBar {
    height: 60px;
    background-color: white;
    padding: 30px 0 5px 30px;
    border-bottom: 1px solid lightgray;
}

.snbLinks {
    max-width: 800px;
    margin: auto;
    display: grid;
    grid-auto-flow: column;
    justify-items: center;
}

.snbLink {
    font-size: 24px;
    text-decoration: none;
    color: #000;
}

.snbLink:hover {
    text-decoration: underline;
}
footer {
  background-color: #08415c;
  min-height: 300px;
  padding: 30px 0;
  color: #eee;
  box-shadow: 0px -2px 2px #08415c;
}

.ftSitemap {
  width: 90%;
  max-width: 1000px;
  margin: 0 auto 40px auto;
  padding: 0 0 20px 0;
  display: grid;
  grid-auto-flow: column;
  justify-items: center;
  justify-content: center;
  grid-column-gap: 200px;
  -webkit-column-gap: 200px;
          column-gap: 200px;
  text-align: center;
  border-bottom: 1px solid #aaa;
}

.ftSitemap h4 {
  font-size: 24px;
  margin: 15px 0;
}

.ftSitemapLink {
  display: block;
  font-size: 16px;
  font-weight: 600;
  margin: 10px 0;
  color: #bbb;
  text-decoration: none;
}

.ftSitemapLink:hover {
  color: black;
}

.ftContent {
  max-width: 1000px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, minmax(180px, 1fr));
  justify-items: center;
  align-items: center;
}

.ftPerson {
  text-align: center;
}

.ftPerson h4 {
  font-size: 20px;
  font-weight: 600;
  margin: 5px 0 10px 0;
}

.ftPerson h5 {
  font-size: 16px;
  font-weight: 600;
  margin: 5px 0;
}

.ftLocation {
  text-align: center;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.75;
  max-width: 200px;
}

.ftMoreContacts {
  color: #ddd;
}

.ftMoreContacts h2 {
  margin: 0;
  font-size: 22px;
}

@media only screen and (max-width: 600px){
  .ftSitemap {
    grid-auto-flow: row;
  }
  .ftPerson:first-of-type {
    grid-row: 1;
    margin-bottom: 30px;
  }
  .ftPerson:last-of-type {
    grid-row: 2;
  }
  .ftContent {
    grid-template-columns: 1fr;
  }
  .ftLocation {
    margin-top: 50px;
    grid-row: 3;
  }
}
