.subNavBar {
    height: 60px;
    background-color: white;
    padding: 30px 0 5px 30px;
    border-bottom: 1px solid lightgray;
}

.snbLinks {
    max-width: 800px;
    margin: auto;
    display: grid;
    grid-auto-flow: column;
    justify-items: center;
}

.snbLink {
    font-size: 24px;
    text-decoration: none;
    color: #000;
}

.snbLink:hover {
    text-decoration: underline;
}